

.Wxwduf{
  display: inline-block;
  font-size: 14px;
  padding: 16px 13px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  vertical-align: top;
  white-space: normal;
  width: calc(100% - 28px);
  border: 1px solid #e1e1e1;
  border-radius: 4px;
  cursor: text;
  background-color: #fff;
}



.Wxwduf .INpyT2 {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-flex: 1;
  box-flex: 1;
  -webkit-flex-grow: 1;
  flex-grow: 1;
  -webkit-flex-shrink: 1;
  flex-shrink: 1;
  min-width: 0%;
  position: relative;
  
}






.INpyT2 input{
    -webkit-box-flex: 1;
    box-flex: 1;
    -webkit-flex-grow: 1;
    flex-grow: 1;
    -webkit-flex-shrink: 1;
    flex-shrink: 1;
    background-color: transparent;
    border: none;
    display: block;
    font: 400 16px Roboto,RobotoDraft,Helvetica,Arial,sans-serif;
    height: 24px;
    line-height: 24px;
    margin: 0;
    min-width: 0%;
    outline: none;
    padding: 0;
    z-index: 0;
    direction: ltr;
    text-align: left;
}

.INpyT2 ._label {
  font-size: 16px;
  background: #fff;
  bottom: 12px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #7e7e7e;
  left: 8px;
  padding: 0 8px;
  -webkit-transition: transform 150ms cubic-bezier(0.4,0,0.2,1),font-size 150ms cubic-bezier(0.4,0,0.2,1);
  transition: transform 150ms cubic-bezier(0.4,0,0.2,1),font-size 150ms cubic-bezier(0.4,0,0.2,1);
  width: auto;
  z-index: 1;
}