.section_slider-cards {
    min-height: 335px;
    background-color: var(--background--color--cards);
    min-height: 0;
    padding: 40px 0 !important;
}


.section_slider-cards .slides-container{
    max-width: 550px;
    margin: 10px auto;
}

.tabs-tab.tab-is-active {
    color: #404040;
}


.tabs-tab {
    border-bottom: 1px solid #f1f1f1;
    color: #4386fc;
    cursor: pointer;
    font-size: 16px;
}
.grid-item, .tabs-tab, .select-menu-wrapper {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    display: inline-block;
    letter-spacing: normal;
    padding: 0;
    text-align: left;
    vertical-align: top;
    width: 100%;
    text-rendering: auto;
}



.tabs-label {
    cursor: pointer;
    display: block;
    padding-bottom: 12px;
    padding-top: 12px;
}




.tabs-label .tabs-label-caption {
    display: none;
}


.tabs-tab.tab-is-active .tabs-label-caption {
    display: block;
}


.tabs-tab .tabs-label-caption {
    color: var(--slide__card--p-color--);
    font-size: 16px;
    font-weight: 200;
    line-height: 1.5;
    margin: 10px 20px 0;
    text-align: initial;
}

.slides-pagination {
    height: 12px;
    list-style: none;
    margin: 0;
    text-align: center;
}
.slides-pagination-item {
    display: inline-block;
    margin: 0 10px;
}

.slides-pagination-item.active .slides-pagination-link {
    background: var(--color-base--hover);
}


.slides-pagination-link {
    background: #e7e7e7;
    border-radius: 50%;
    display: block;
    height: 12px;
    text-indent: -9999px;
    width: 12px;
    cursor: pointer;
}

