    .mqn2-b34 {
       /* bottom: 6.25vw;*/
        position: absolute;
        text-align: center;
        width: 100%;
    }

    .mqn2-aao:last-child {
        margin-right: 0;
    }
    
    .mqn2-aao.mqn2-aap, .mqn2-aao.mqn2-aat, .mqn2-aao.mqn2-aas {
        position: relative;
    }
    .mqn2-body a, .ak-modal-content a {
        text-decoration: none;
    }
    
    .mqn2-aao {
        margin-right: 10px;
    }
    .mqn2-aat {
        border-color: rgba(126,126,126,0.3);
        color: #423F3F !important;
    }

    
    .mqn2-aao {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        padding: 6px 12px;
        background: transparent;
        border: none;
        border-width: 2px;
        border-style: solid;
        border-radius: 3px;
        text-align: center;
        outline: none;
        font-weight: 500;
    }


  

    .mqn2-aao.mqn2-aap:before, .mqn2-aao.mqn2-aat:before, .mqn2-aao.mqn2-aas:before {
        content: "";
        opacity: 0.0;
        transform-origin: center center;
        transform: scale3d(0.9, 0.65, 0.8);
        transition: all 250ms cubic-bezier(0.1, 0.1, 0, 1);
        position: absolute;
        top: -2px;
        right: -2px;
        width: calc(100% + 4px);
        height: calc(100% + 4px);
        border-radius: 3px;
        background-color: #423F3F;
    }

    .mqn2-b34:hover .mqn2-aao:hover.mqn2-aat:before{
        opacity: 1;
    }

    .mqn2-b34:hover .mqn2-aaq span{
        color:#fff;
    }

    .mqn2-aaq {
        display: inline-flex;
        justify-content: center;
        align-items: center;
    }

